    AcordionA(tipo="a" clase-tarjeta="tarjeta tarjeta--grisos")(data-aos="zoom-in-left")
      .row(titulo="Urdimbre y trama")
        .col-md-7.mb-4.mb-md-0
          p.ps-5.p-3 #[b Urdimbre: ] la serie longitudinal de los hilos recibe el nombre de urdimbre, y cada uno de los elementos que la constituyen se denomina hilo. Los hilos verticales son la urdimbre.
          p.ps-5.p-3 #[b Trama: ] la serie transversal recibe el nombre de trama, y cada una de sus unidades se denomina pasada. Las pasadas horizontales son la trama.
        .col-md-3
            img(src='@/assets/curso/temas/tema2/imagen17.png', alt='La imagen expone un diagrama de un tejido con indicaciones de los componentes principales. La Urdimbre est indicada con una flecha roja y corresponde a los hilos longitudinales que se mantienen tensos en un telar. La Trama est indicada con una flecha azul y corresponde a los hilos transversales que se entrelazan con la urdimbre. Debajo del diagrama principal, hay tres ilustraciones que describen diferentes patrones de entrelazado de hilos: a la izquierda se expone el esquema de la urdimbre, en el centro la forma en que se teje la trama, y a la derecha la combinacin de ambos elementos en el tejido final.')
      .row(titulo="Hilo de tela")
        .col-md-7.mb-4.mb-md-0
          p.ps-5.p-3 El hilo de tela se conoce tambin como sentido de hilo, y se refiere a la direccin que siguen los hilos de urdimbre (longitudinal) y trama (transversal).
          p.ps-5.p-3 La lnea que atraviesa la urdimbre y la trama en sentido diagonal es conocida con el nombre de sesgo o bies.
        .col-md-3
          img(src="@/assets/curso/temas/tema2/imagen18.png" alt="La imagen describe  tres piezas de un patrn de costura sobre un fondo de tela con puntos. Cada pieza del patrn tiene flechas rojas indicando la direccin del hilo de la tela. La pieza a la izquierda es de forma irregular, probablemente una manga, con las flechas indicando la direccin del hilo paralelo al borde inferior. Las otras dos piezas, que parecen ser partes del cuerpo de una prenda, tambin tienen flechas indicando la direccin del hilo a lo largo del largo de las piezas. Cada pieza del patrn tiene un pequeo rectngulo blanco en el centro, que es una etiqueta o una marca de referencia." )
      .row(titulo="Sesgo o bies")
        .col-md-6.mb-4.mb-md-0
          p.ps-5.p-3 Se llama sesgo o bies al sentido diagonal de las telas en un ngulo de 45 grados; diagonal o al sesgo aporta mejor cada y cierta elasticidad. En algunos casos es necesario cortar las piezas de una prenda al sesgo debido a su diseo.
        .col-md-5
          img(src="@/assets/curso/temas/tema2/imagen19.png" alt="La imagen ilustra la orientacin de los patrones en la tela, mostrando las direcciones del recto hilo, el contrahlo y el bies. A la izquierda, se presenta un cuadrado de tela con las direcciones sealadas. A la derecha, se muestra cmo colocar los patrones en la tela, alinendolos segn el recto hilo y el centro del pliegue para asegurar un corte preciso y adecuado. " )
  
