          h4.text-center Ancho simple
          p.text-center Estn elaborados a partir de 70 cm a 90 cm de ancho, en este grupo se encuentran cambre, paos, fieltro, blonda, satn, lino, etc.
      .col-sm-6.col-lg-3.mb-4.mb-lg-0
        .tarjeta--boton.color-adicional-2.p-4
