              img(src="@/assets/curso/temas/tema2/tarjeta4/imagen2.png") 
          h4.text-center Ancho angosto
          p.text-center Estn elaborados a partir de 0,5 mm hasta 25 cm de ancho, en este grupo se encuentran los encajes, franjas, cintas, elsticos, etc.
      .col-sm-6.col-lg-3.mb-4.mb-sm-0
        .tarjeta--boton.color-adicional-2.p-4
          .row.justify-content-center.mb-3
            .col-7
