          p.text-center Estn elaborados a partir de 140 cm y 150 cm de ancho, en este grupo se encuentran paos, tafetn, popelina, crep, entretelas, denim, etc.
      .col-sm-6.col-lg-3
        .tarjeta--boton.color-adicional-2.p-4
          .row.justify-content-center.mb-3
            .col-7
              img(src="@/assets/curso/temas/tema2/tarjeta4/imagen4.png") 
          h4.text-center Ancho especial
          p.text-center.mb-4.p-1 Estn elaborados a partir de 2,80 cm, en este  grupo se encuentran la  tapicera y la decoracin.
        
    .row.justify-content-center.mb-4.mt-5(data-aos="zoom-in-left")
