        h3.mb-4.mt-4 Orillos de tela
        p.mb-2 Son los bordes longitudinales de la tela, estn tejidos con una apariencia diferente y densa, como consecuencia de tratamientos de humectacin y secado, que sufren una vez han sido tejidas para su estabilizacin.
      .col-10.col-md-5.col-lg-5
        img.p-2(src="@/assets/curso/temas/tema2/imagen21.png" alt="La figura describe dos imgenes de primeros planos de orillos de tela. El orillo es el borde terminado de la tela, que no se deshilacha. En la imagen a la izquierda, se observa un orillo blanco con texto impreso en color oscuro que parece ser parte del nombre de la empresa fabricante. La imagen a la derecha muestra varios orillos de diferentes telas, cada uno con colores y patrones distintos, adems de texto impreso que incluye nombres de diseadores, fabricantes y posiblemente informacin de copyright. Estos orillos muestran una variedad de colores y diseos, indicando la diversidad en la produccin textil." )

    br
    .row.justify-content-center.mb-4.mt-4(data-aos="zoom-in-left")
      .col-sm-12.col-md-12.col-lg-10
        .tarjeta.color-acento-botones.borde-redondo
          .row.justify-content-around.align-items-center
            .col-6.col-md-3.col-lg-2
              img.mt-4.mb-4.ps-5(src="@/assets/curso/temas/tema2/admiracion.png")
            .col-sm-12.col-md-9.col-lg-10
              p.p-3.mb-0 #[b Cmo surgen? ]
              p.ps-3 Los agujeritos de los orillos son realizados por unas plaquetas de agujas situadas en los laterales de los hornos encargados de secar las telas y que hacen que estas se mantengan sujetas y estiradas en su ancho durante todo el proceso. Suelen perforar el tejido desde el revs hacia el derecho, por lo que, por regla general, las rebabas de las perforaciones en los orillos indican el #[b derecho de la tela. ]


    .subt.mb-5.mt-5(data-aos="fade-right")
      .d-flex.align-items-center.mb-1
