        h3.mb-0.ms-2 Tipos de orillos
    p El propsito del orillo es que la tela no se deshilache. Segn el diseo o fabricante los orillos de la tela se dividen en:
    
    .row.mt-4.mb-4(data-aos="zoom-in-left")
      .col-sm-12.col-md-7.col-lg-7
        LineaTiempoD.color-primario
          .row(numero="1" titulo="Orillo metido")
            .col-md-12.mb-4.mb-md-0
              p Una aguja especial en forma de gancho impulsada por una leva, despus de que esta se corta, la insercin del extremo del hilo retorna, formando as un borde fuerte. Este sistema es usado generalmente en las telas de peso ligero a medio.
          .row(numero="2" titulo="Orillo leno")
            .col-md-12.mb-4.mb-md-0
              p Estos orillos se obtienen uniendo la trama con fuertes hilos adicionales y eliminando mediante corte los extremos de la trama que sobresalen. Este sistema es usado generalmente en las telas de peso pesado.  
          .row(numero="3" titulo="Orillo de puntada cadeneta")
            .col-md-12.mb-4.mb-md-0
              p Este tipo de orillo se produce mayoritariamente en un mecanismo de tejido de punto, se realiza principalmente en una mquina de tejer por medio de agujas. 
          .row(numero="4" titulo="Orillos fusionados")
            .col-md-12.mb-4.mb-md-0
              p Se obtiene presionando unos elementos mecnicos calientes en el borde de la tela, este mtodo se puede aplicar en textiles de fibras sintticas. 
          .row(numero="5" titulo="Orillos lisos")
            .col-md-12.mb-4.mb-md-0
              p Este tipo de orillo tambin llamado orillo de estampacin es usado para controlar los colores bsicos que tiene el dibujo, enumerndolos en el orden de estampacin, adems aade la marca del fabricante. 
          .row(numero="6" titulo="Orillos de cinta")
            .col-md-12.mb-4.mb-md-0
              p Este tipo de orillo es utilizado por los fabricantes de textiles clsicos como paos, tambin tejen sus nombres en el orillo para que sean identificados fcilmente en las sastreras a medida. 
      .col-8.col-md-4.col-lg-4
        img.p-3.mt-4(src="@/assets/curso/temas/tema2/imagen22.png", alt='telas')

