        h3 Qu es el extendido?
        p El tendido o extendido consiste en ubicar la tela sobre una superficie plana (mesa de corte), ya sea una capa de tela o varias segn la orden de produccin; se extiende la tela considerando las caractersticas del textil, se prepara y selecciona los materiales e instrumentos necesarios para la ejecucin de la tarea; el extendido se puede realizar de dos formas: 

