      .col-sm-12.col-md-7.col-lg-7
        h3.mt-4.mb-4 Preparacin del textil
        p Algunos textiles se pueden encoger o aumentar despus de ser sometidos al lavado o planchado, por tal motivo, se sugiere que antes de realizar cualquier proceso de extendido y corte, el textil sea sometido a lo siguiente:
        ul.lista-ul--color.fa-ul.color-primario
          li 
            i.fas.fa-plus
