    .row.mt-4.mb-4(data-aos="zoom-in-left")
      h3.mb-4 Disposicin de la tela sobre la mesa
      p Extienda el textil sobre la mesa y defina el tipo de extendido de acuerdo con los patrones que desea trabajar: 

    .tarjeta.tarjeta--azul.p-4.mb-4.borde-redondo(style="background-color: #F8F5FF")(data-aos="zoom-in-left")
      SlyderA(tipo="b")
        .row.justify-content-center.my-lg-4
          p #[b Tendido abierto  ]
          .col-md-10
            img(src="@/assets/curso/temas/tema2/tarjeta6/imagen1.svg", alt='La imagen presenta cmo alinear y cortar patrones en la tela. A la izquierda, se ven patrones colocados sobre una tela, alineados con el recto hilo y el orillo de la tela para asegurar un corte preciso. A la derecha, se ilustra cmo una pieza del patrn debe estar alineada con el orillo de la tela para que salga completa y correctamente cortada. Las instrucciones enfatizan la importancia de seguir la direccin del recto hilo y el orillo para obtener un resultado adecuado. La imagen muestra cmo alinear y cortar patrones en la tela. A la izquierda, se ven patrones colocados sobre una tela, alineados con el recto hilo y el orillo de la tela para asegurar un corte preciso. A la derecha, se ilustra cmo una pieza del patrn debe estar alineada con el orillo de la tela para que salga completa y correctamente cortada. Las instrucciones enfatizan la importancia de seguir la direccin del recto hilo y el orillo para obtener un resultado adecuado. La imagen muestra cmo alinear y cortar patrones en la tela. A la izquierda, se ven patrones colocados sobre una tela, alineados con el recto hilo y el orillo de la tela para asegurar un corte preciso. A la derecha, se ilustra cmo una pieza del patrn debe estar alineada con el orillo de la tela para que salga completa y correctamente cortada. Las instrucciones enfatizan la importancia de seguir la direccin del recto hilo y el orillo para obtener un resultado adecuado. La imagen muestra cmo alinear y cortar patrones en la tela. A la izquierda, se ven patrones colocados sobre una tela, alineados con el recto hilo y el orillo de la tela para asegurar un corte preciso. A la derecha, se ilustra cmo una pieza del patrn debe estar alineada con el orillo de la tela para que salga completa y correctamente cortada. Las instrucciones enfatizan la importancia de seguir la direccin del recto hilo y el orillo para obtener un resultado adecuado. ')
        .row.justify-content-center.my-lg-4
          p #[b Tendido cerrado  ]
          .col-md-10
            img(src="@/assets/curso/temas/tema2/tarjeta6/imagen2.svg", alt='La imagen presenta cmo alinear y cortar patrones en la tela. A la izquierda, se ven patrones colocados sobre una tela, alineados con el recto hilo y el orillo de la tela para asegurar un corte preciso. A la derecha, se ilustra cmo una pieza del patrn debe estar alineada con el orillo de la tela para que salga completa y correctamente cortada. Las instrucciones enfatizan la importancia de seguir la direccin del recto hilo y el orillo para obtener un resultado adecuado.')

    .row.mt-4.mb-4(data-aos="zoom-in-left")
      h3.mb-4.mt-4 Extendido industrial
      p El proceso de extendido industrial se realiza a travs del  #[em software ] especializado (CAD, sistemas de diseo asistido por computador: Optitex, Lectra, Audaces). Este proceso se realiza segn las especificaciones de la orden de corte o ficha tcnica. Los extendidos utilizados en la industria son:

    AcordionA(tipo="b" clase-tarjeta="tarjeta tarjeta--purple")(data-aos="zoom-in-left")
      .row.borde-redondo-tmorada(titulo="Extendido cara arriba")
        .col-md-4.mb-4.mb-md-0
          p.p-5 La capa de tela se extiende segn el largo del trazo, es importante determinar cul es el derecho de la tela, ya que esta debe quedar mirando hacia arriba con respecto a la mesa. Se extiende la capa desde el inicio del trazo hasta el final del largo del trazo y se procede a cortar; nuevamente se desplaza la siguiente capa desde el inicio y se repite el proceso el nmero de veces que requiera las capas.
        .col-md-8
          img.p-5(src="@/assets/curso/temas/tema2/tarjeta7/imagen1.svg" alt="La figura es un diagrama de varias capas de tela apiladas, indicando partes clave como el Derecho de la tela y el Orillo. Las flechas sealan la Direccin de la tela a lo largo de las capas, que estn etiquetadas con direccin del tejido y derecho del tejido. Es crucial extender la capa de tela segn el largo del trazo y determinar cul es el derecho de la tela para asegurar que todas las piezas se corten en la misma orientacin, manteniendo la consistencia en la apariencia y el comportamiento del tejido en la prenda final. ")
      .row.borde-redondo-tmorada(titulo="Extendido al lomo o doblez de tela")
        .col-md-4.mb-4.mb-md-0
          p.p-5 Se realiza cuando la tela se dobla a la mitad de su ancho, se une orillo con orillo formando un doblez de tela en un extremo, conocido como lomo. Al doblar la tela queda derecho con derecho, lo que se denomina careado o cara a cara. Este tipo de extendido se utiliza para cortar prototipos, es utilizado por modistas o sastres.
        .col-md-8
          img.p-5(src="@/assets/curso/temas/tema2/tarjeta7/imagen2.svg" alt="La figura describe un diagrama en blanco y negro de varias capas de tela apiladas, indicando claramente el Derecho de la tela, el Revs de la tela, y el Doblez de tela. Las flechas sealan la Direccin de la tela y los Orillos, que son los bordes terminados. Cada capa est etiquetada con direccin del tejido y derecho del tejido. Es crucial extender la capa de tela segn el largo del trazo y determinar cul es el derecho de la tela para asegurar que todas las piezas se corten en la misma orientacin, manteniendo la consistencia en la apariencia y el comportamiento del tejido en la prenda final. ")
      .row.borde-redondo-tmorada(titulo="Extendido cara a cara")
        .col-md-4.mb-4.mb-md-0
          p.p-5 La capa de tela se extiende segn el largo del trazo, es importante determinar cul es el derecho de la tela, ya que la primera capa debe quedar con el derecho hacia arriba con respecto a la mesa. Se extiende la capa desde el inicio del trazo hasta el final del largo del trazo y se procede a cortar.
          p.ps-5 Se repite el proceso con la siguiente capa, pero teniendo en cuenta que esta debe quedar con el derecho de tela hacia abajo, quedando cada a cara con la primera capa. Las siguientes capas deben quedar en pares, derecho con derecho de la tela.
        .col-md-8
          img.p-5(src="@/assets/curso/temas/tema2/tarjeta7/imagen3.svg" alt="La figura describir un diagrama en blanco y negro de varias capas de tela apiladas, destacando el Derecho de la tela y el Revs de la tela. Las flechas indican la Direccin de la tela a lo largo de las capas. Cada capa est etiquetada con direccin del tejido, derecho del tejido y revs del tejido. Es crucial extender la capa de tela segn el largo del trazo y determinar cul es el derecho de la tela para asegurar que todas las piezas se corten en la misma orientacin, manteniendo la consistencia en la apariencia y el comportamiento del tejido en la prenda final. ")
      .row.borde-redondo-tmorada(titulo="Extendido  <em>zigzag</em>")
        .col-md-4.mb-4.mb-md-0
          p.p-5 El proceso es igual al extendido cara a cara, la diferencia es que no se cortan los extremos. Una vez llegado al extremo dobla sobre s mismo para hacer el recorrido en sentido contrario. De esta forma las capas de tela deben quedar pares cara con cara y revs con revs.
        .col-md-8
          img.p-5(src="@/assets/curso/temas/tema2/tarjeta7/imagen4.svg" alt="La figura expone un diagrama en blanco y negro de varias capas de tela apiladas, destacando las etiquetas de Derecho de la tela y Revs de la tela. Las flechas indican la Direccin de la tela y sealan que las capas estn dobladas, lo que tambin se seala con la etiqueta doblez de tela en una de las capas. Cada capa est etiquetada con direccin del tejido, derecho del tejido y revs del tejido. Es crucial extender la capa de tela segn el largo del trazo y determinar cul es el derecho de la tela para asegurar que todas las piezas se corten en la misma orientacin, manteniendo la consistencia en la apariencia y el comportamiento del tejido en la prenda final. ")
      .row.borde-redondo-tmorada(titulo="Extendido escalonado o espiga")
        .col-md-4.mb-4.mb-md-0
          p.p-5 Es un extendido cara arriba en el que los largos de las capas de tela no son iguales. Las capas ms largas se colocan debajo y las ms cortas se colocan arriba formando un escaln, este tipo de extendido se emplea para ajustar en un solo trazo diferentes cantidades en relacin con tallas y colores de prendas a cortar.
        .col-md-8
          img.p-5(src="@/assets/curso/temas/tema2/tarjeta7/imagen5.svg" alt="Se exponen dos figuras relacionados con el apilamiento y corte de telas. En la parte superior, se expone un apilamiento de capas de tela, cada una etiquetada con direccin del tejido y derecho del tejido. En la parte inferior, se presenta un apilamiento de telas con patrones de corte dispuestos sobre ellas, listos para ser cortados. Los patrones estn alineados y optimizados para el corte, asegurando que todas las piezas se corten de manera consistente y eficiente. Este proceso es crucial para mantener la orientacin correcta del tejido y asegurar la calidad de las piezas finales.")

