    .row.mb-4.mt-4
      h3.text-center Sealizacin - Hilos de tela
      p.text-center Para tener claro los requerimientos desde el rea de patronaje es importante identificar y conocer la respectiva sealizacin de patrones:

    .row.justify-content-center.mt-4.mb-4(data-aos="zoom-in-left")
      .col-sm-12.col-md-6.col-lg-3
        .tarjeta.color-primario.borde-redondo.p-4
          img.p-4(src="@/assets/curso/temas/tema2/tarjeta9/imagen1.png", alt="La imagen describe tres cuadros explicativos sobre el tejido textil. El primero, titulado Urdimbre, ilustra los hilos longitudinales de la tela; el segundo, Trama, muestra los hilos transversales; y el tercero, Sesgo o bies, indica una tela cortada en un ngulo de 45 grados para mayor elasticidad. Debajo, una frase mnemotcnica ayuda a recordar la correcta orientacin de los hilos: Hilo de tela una cabeza hacia arriba, hilo de tela una cabeza hacia abajo, hilo de tela una cabeza doble.")
          p.text-center #[b Urdimbre ]
      .col-sm-12.col-md-6.col-lg-3
        .tarjeta.color-acento-botones.borde-redondo.p-4
          img.p-4(src="@/assets/curso/temas/tema2/tarjeta9/imagen2.png", alt="La imagen describe tres cuadros explicativos sobre el tejido textil. El primero, titulado Urdimbre, ilustra los hilos longitudinales de la tela; el segundo, Trama, muestra los hilos transversales; y el tercero, Sesgo o bies, indica una tela cortada en un ngulo de 45 grados para mayor elasticidad. Debajo, una frase mnemotcnica ayuda a recordar la correcta orientacin de los hilos: Hilo de tela una cabeza hacia arriba, hilo de tela una cabeza hacia abajo, hilo de tela una cabeza doble.")
          p.text-center.color-white #[b Trama ]
      .col-sm-12.col-md-6.col-lg-3
        .tarjeta.color-primario.borde-redondo.p-4
          img.p-4(src="@/assets/curso/temas/tema2/tarjeta9/imagen3.png", alt="La imagen describe tres cuadros explicativos sobre el tejido textil. El primero, titulado Urdimbre, ilustra los hilos longitudinales de la tela; el segundo, Trama, muestra los hilos transversales; y el tercero, Sesgo o bies, indica una tela cortada en un ngulo de 45 grados para mayor elasticidad. Debajo, una frase mnemotcnica ayuda a recordar la correcta orientacin de los hilos: Hilo de tela una cabeza hacia arriba, hilo de tela una cabeza hacia abajo, hilo de tela una cabeza doble.")
          p.text-center #[b Sesgo o bies ]
      p.mb-4.mt-4.text-center Hilo de tela una cabeza hacia arriba, hilo de tela una cabeza hacia abajo, hilo de tela una cabeza doble.

    AcordionA(tipo="a" clase-tarjeta="tarjeta tarjeta--grisos")(data-aos="zoom-in-left")
      .row(titulo="Trazo bidireccional, vello en ambos sentidos")
        .col-md-6.mb-4.mb-md-0
          p.ps-5 Este trazo/marcado se realiza cuando el hilo de tela tiene la cabeza doble; se pueden colocar los patrones en dos sentidos de direccin.
          p.ps-5 El material textil que se utilice no debe tener restriccin con respecto a sus caractersticas.
          p.ps-5.mb-0 Hilo de tela una cabeza doble.
        .col-md-5
            img(src='@/assets/curso/temas/tema2/imagen28.png', alt='La imagen expone cmo posicionar patrones de una prenda en la tela, destacando la orientacin del hilo de tela. Se exponen tres piezas del patrn con flechas rojas que indican la direccin del hilo. Las instrucciones resaltan la necesidad de colocar el hilo de la tela de una camisa hacia arriba o hacia abajo segn corresponda, asegurando que todas las piezas se corten correctamente alineadas con el hilo para mantener la coherencia en el diseo final de la prenda. ')
      .row(titulo="Bidireccional, vello en ambos sentidos")
        .col-md-6.mb-4.mb-md-0
        .col-md-5
          img(src="@/assets/curso/temas/tema2/imagen29.png", alt="La imagen detalla la disposicin de patrones en la tela, destacando la orientacin correcta respecto al borde cortado y el orillo. En ambos ejemplos, las piezas del patrn estn alineadas con flechas rojas que indican la direccin del hilo. A la izquierda, las piezas estn colocadas cerca del orillo y el borde cortado, mientras que a la derecha, las piezas estn alineadas con el orillo y el borde cortado de la tela, asegurando que las piezas se corten correctamente siguiendo la direccin del hilo para una confeccin precisa. ")
      .row(titulo="Trazo direccional, vello en un solo sentido")
        .col-md-9.mb-4.mb-md-0
          p.ps-5 Este trazo/marcado se realiza cuando el hilo de tela tiene la cabeza sea hacia arriba o hacia abajo; se deben colocar los patrones en un sentido de direccin. El procedimiento de marcado tendr por resultado un producto con la ms elevada calidad. Este tipo de trazo se usa para materiales que tienen restriccin con respecto a sus caractersticas en cuanto a brillo, cambio de color al tacto, estampados, direccin de pelo/vello, entre otros.
          p.ps-5.mb-0 Hilo de tela una cabeza hacia arriba - Hilo de tela una cabeza hacia abajo
        .col-md-2
