        img(src="@/assets/curso/temas/tema2/imagen35.png" alt="La imagen ilustra cmo medir y demarcar el ancho til de una pieza de tela. Se muestra que el ancho total de la tela es de 160 cm, con un orillo de 2 cm a cada lado, lo que reduce el ancho til a 156 cm. Las instrucciones indican marcar el ancho til con lpiz o tiza para asegurar que las piezas del patrn se corten dentro de estas dimensiones, manteniendo el orillo fuera del rea de corte. Esta prctica es esencial para maximizar el uso de la tela y garantizar que las piezas cortadas tengan la anchura correcta. ")
  
