            img(src="@/assets/curso/temas/tema2/tarjeta11/imagen1.png" alt="La imagen expone una coleccin de herramientas de corte para textiles, cada una enmarcada en un crculo con fondo morado. Comienza con las Tijeras castellana, diseadas con orificios iguales y recomendadas para textiles ligeros. Siguen las Tijeras sastre, con un diseo que permite apoyarlas en la mesa, adecuadas para tejidos de peso mediano a pesado. Las Tijeras zigzag tienen hojas dentadas que cortan en zigzag para prevenir el deshilachado de los textiles. El Cortador trmico se utiliza para cortar y sellar simultneamente acrlico y textiles, evitando el deshilachado. Finalmente, el Cter rotatorio es ptimo para cortar una variedad de materiales, desde telas hasta vinilos, con un mango que mejora la precisin y facilita el uso con reglas para mayor exactitud en el corte.")
        p.text-center #[b Tijeras castellana ]
