              h4.mb-4.text-center #[b Caractersticas del material ]
    
    .row.mb-4.mt-4
      p.mt-4.mb-4 #[b Para qu? ]
      p.mb-0 El reconocimiento de las caractersticas del material textil a trabajar es sumamente importante para asegurar una calidad ptima en la prenda al cortar y ensamblar. Antes de cada proceso de tendido, marcacin y corte se deben verificar dichas caractersticas.
    
    .row.justify-content-center.mb-4-mt-4
      .col-lg-9
        img.p-4(src="@/assets/curso/temas/tema2/imagen45.png" alt="La imagen detalla dos ilustraciones que explican la direccin del pelo en las telas. A la izquierda, se presentan dos direcciones del pelo en la tela, una hacia arriba y otra hacia abajo, indicadas con flechas. A la derecha, una mano acaricia una tela en la direccin del pelo, demostrando cmo se debe sentir la textura para determinar la orientacin correcta. Estas guas son importantes para asegurar que todas las piezas del patrn se corten en la misma direccin del pelo, evitando diferencias de color y textura en la prenda final. ")

    .row.justify-content-center.mb-4-mt-4
      .col-sm-12.col-md-8.col-lg-9
        p.mb-3.mt-5 #[b Qu es la textura #[em nap] (#[em velvet], pelusa, #[em fleece])?]
        p Nap es la textura de los textiles que van en una direccin particular. Esta propiedad de la prenda es fundamental para garantizar la calidad de la misma. En algunos textiles se logra evidenciar el nap a simple vista como #[em velvet, fleece,] piel sinttica, denim cepillado, franela, gamuza sinttica, sarga, elstica, tela de felpa, etc.
        .tarjeta.color-acento-botones.borde-redondo.mt-4
          .row.justify-content-around.align-items-center
            .col-6.col-md-4.col-lg-2
              img.mt-4.mb-4.ps-5(src="@/assets/curso/temas/tema2/admiracion.png")
            .col-sm-12.col-md-8.col-lg-10
              p.ps-3.mb-0 #[b Por qu es importante el #[em nap]? ]
              p.ps-3.mb-0 El #[em nap] es muy importante para determinar cmo se establece el patrn en el textil, en este caso se trabaja siempre un trazo direccional. Se debe tener en cuenta lo siguiente:
      .col-6.col-md-4.col-lg-3
        img.p-2(src="@/assets/curso/temas/tema2/imagen46.png")

    .row.justify-content-center.align-items-center.mb-5.mt-3
      .col-sm-12.col-md-5.col-lg-5.p-3
        .row.estilo1
          .col-8.col-md-3.col-lg-3
            img.p-2.mt-3(src='@/assets/curso/temas/tema2/uno.svg' alt='AvatarTop')
          .col-sm-12.col-md-9.col-lg-9
            p.p-2.mt-3 #[b Acabados de la prenda ]
            p.ps-2.mb-5 Si no se tiene en cuenta el  #[em nap ] al momento de cortar la prenda, la prenda terminara con brillos diferentes en cada pieza, necesitan un color y una textura uniforme en toda la pieza.
      .col-sm-12.col-md-5.col-lg-5.p-3
        .row.estilo1
          .col-8.col-md-3.col-lg-3
            img.p-2.mt-3(src='@/assets/curso/temas/tema2/dos.svg' alt='AvatarTop')
          .col-sm-12.col-md-9.col-lg-9
            p.p-2.mt-3 #[b Determinar el consumo del textil ]
            p.ps-2.mb-4 Se debe determinar el consumo de material que requiere la prenda para garantizar que todo el textil sea suficiente para el corte, lo cual es una clara desventaja, ya que se desperdicia mucha tela al seguir un trazo direccional.

    .row.justify-content-center.mb-4-mt-4
      .col-sm-12.col-md-12.col-lg-5
        p #[b Cmo identificar el #[em nap]?]
        p Al pasar las manos suavemente por el textil para sentir el  #[em nap]/pelusa
        ul.lista-ul--color.fa-ul.color-primario
          li 
            i.fas.fa-plus
            span.mb-0 La sensacin ms suave es la direccin con  #[em nap]/pelusa.
          li
            i.fas.fa-plus
            span.mb-0 La sensacin ms spera es la direccin contraria al  #[em nap]/pelusa.
          li
            i.fas.fa-plus
            span.mb-0 El brillo de la tela es diferente cuando se ve desde diferentes direcciones.
      .col-10.col-md-7.col-lg-7
        img(src="@/assets/curso/temas/tema2/imagen48.svg" alt="La imagen detalla la correcta e incorrecta direccin del pelo al cortar piezas de un patrn. La seccin superior, marcada como Correct, indica que todas las piezas del patrn estn alineadas con el pelo en la misma direccin, lo cual es correcto. La seccin inferior, marcada como Incorrect, expone piezas del patrn con el pelo en diferentes direcciones, lo cual es incorrecto. Esto asegura que la textura y apariencia de la prenda final sean uniformes. ")

    .row.justify-content-center.align-items-center.mb-5.mt-5
      .col-sm-12.col-md-12.col-lg-3
        p #[b Trazo o marcado ]
        p Se recomienda que cuando se utilice este tipo de textiles con #[em nap], el proceso de transferir los moldes al textil se trabaje preferiblemente en un tendido abierto, sujetado con alfileres. Para marcar pinzas o elementos clave se recomienda los hilvanes o  #[em tailor tacks].
      .col-8.col-md-7.col-lg-7
        img(src="@/assets/curso/temas/tema2/imagen49.png" alt="La imagen expone dos pasos en el proceso de confeccin. A la izquierda, se ve un patrn colocado sobre la tela con marcas realizadas con hilos para indicar puntos importantes del diseo. A la derecha, una persona utiliza tijeras para cortar la tela siguiendo las marcas del patrn. Estas imgenes destacan la importancia de marcar correctamente la tela antes de cortarla para asegurar precisin en el ensamblaje de la prenda.")

    .row.justify-content-center.mb-5-mt-5
      .col-sm-12.col-md-7.col-lg-7
        p #[b Piel sinttica ]
        p La piel sinttica tambin cuenta con  #[em nap]/siesta que es mucho ms sencilla para identificar la direccin del vello. En este caso se debe trabajar:
        ul.lista-ul--color.fa-ul.color-primario
          li 
            i.fas.fa-plus
            span.mb-0 Trazo direccional.
          li
            i.fas.fa-plus
            span.mb-0 Tendido abierto.
          li
            i.fas.fa-plus
            span.mb-0 Marcacin por el revs con tiza.
        p Algunas tienen el vello ms largo que otras, por eso es importante realizar un trazo, marcado y corte de forma adecuada.
      .col-7.col-md-5.col-lg-3
        img.mt-3(src="@/assets/curso/temas/tema2/imagen50.svg")

    .row.justify-content-center.align-items-center.mb-5.mt-5
      .col-sm-12.col-md-10.col-lg-10
        .row.estilo2
          .col-sm-12.col-md-6.col-lg-6
            .row.bgamarillo
              p.mt-5.ps-5 #[b Tips de corte, piel sinttica ]
              p.p-5 Cortar en el sentido del  #[em nap]/vello. Realice cortadas pequeas, preferible con la punta de la tijeras. Una vez cortado se sugiere eliminar el exceso de vello/pelo por el margen de costura, teniendo en cuenta la direccin del pelo se recomienda confeccionarlo en esa misma direccin.
          .col-10.col-md-6.col-lg-6
            .row.align-items-center
              img.mt-5(src="@/assets/curso/temas/tema2/imagen51.svg" alt="La imagen da las  instrucciones para cortar y trabajar con telas de pelaje o velour. Primero, se debe cortar en el sentido del velln o nap, como se ilustra con una mano alineando el corte. Es recomendable realizar cortes pequeos, preferiblemente con la punta de la tijera, como se observa en una imagen de tijeras cortando tela rosa. Adems, se aconseja tener en cuenta la direccin del pelo y confeccionarlo en esa misma direccin, mostrada en una tela gris con el pelo alineado. Finalmente, una vez cortado, se sugiere eliminar el exceso de velln o pelo por el margen de costura, como se ve en la imagen de la tela rosa despus del corte.")

    .row.justify-content-center.mb-4-mt-4
      .col-sm-col-md-4.col-lg-4
        p #[b Textiles estampados ]
        p Los textiles estampados tambin se deben trabajar con un trazo direccional, ya que de esto depende la direccin del estampado que se desee en la prenda terminada. Esto significa que todas las piezas deben cortarse en la misma direccin para que el estampado quede acorde al diseo.
      .col-6.col-md-6.col-lg-6
        img(src="@/assets/curso/temas/tema2/imagen52.svg" alt="La imagen expone el proceso de colocar patrones sobre la tela para el corte. En la parte superior derecha, se ve una tela extendida con varias piezas del patrn colocadas, alineadas con el hilo y el orillo de la tela. En la parte inferior izquierda, se muestran dos piezas del patrn, con una marcada como centro frente, indicando la alineacin central en la tela. Estas imgenes enfatizan la importancia de alinear correctamente los patrones con el hilo y el orillo de la tela para garantizar precisin en el corte y la confeccin de la prenda.")

    .row.align-items-center.justify-content-center.mt-4
      .col-7.col-md-3.col-lg-3.p-3
        img(src="@/assets/curso/temas/tema2/imagen53.png")
      .col-sm-12.col-md-7.col-lg-7
        p #[b Textiles de cuadros ]
        p La continuidad de las lneas y el dibujo/cuadros en las prendas producen un efecto agradable a la vista.
        .cajon.color-primario 
          p.p-5 #[b  Hacer coincidir cada una de las lneas, cuadros o elementos del dibujo del textil requiere precisin; sin embargo, esto se utiliza en prendas de muy altos estndares de calidad, ya que el corte se debe hacer preferiblemente individual.  ]

    .row.justify-content-center.mb-4.mt-5
      .col-sm-12.col-md-5.col-lg-5
        p #[b Tendido y corte ]
        p Para los textiles de cuadros se recomienda trabajarlos de la siguiente manera:
        ul.lista-ul--color.fa-ul.color-primario
          li 
            i.fas.fa-plus
            | Trabajar despiece sin costura.
          li
            i.fas.fa-plus
            | Trazo direccional.
          li
            i.fas.fa-plus
            | Tendido abierto cara arriba.
          li
            i.fas.fa-plus
            | Marcacin por el revs con tiza.
          li
            i.fas.fa-plus
            | Fijar los patrones con alfileres.
      .col-sm-12.col-md-5.col-lg-5
        img.mt-5(src="@/assets/curso/temas/tema2/imagen54.png" alt="La imagen presenta dos diagramas de una chaqueta a cuadros, destacando diferentes partes y detalles de la prenda con nmeros y crculos. Estos diagramas probablemente sealan caractersticas especficas de la chaqueta, como costuras, botones, bolsillos y otros elementos de diseo tanto en la vista frontal como en la vista posterior. La anotacin numrica facilita la identificacin y referencia a las distintas partes de la chaqueta para su anlisis o confeccin. ")

    .row.justify-content-center.align-items-center.mb-4.mt-5
      p.text-center #[b Tips de corte para cuadros ]

    .row.justify-content-center.mt-4.mb-4(data-aos="zoom-in-left")
      .col-sm-12.col-md-6.col-lg-3
        .tarjeta.color-acento-botones.borde-redondo.p-4
          img.p-4(src="@/assets/curso/temas/tema2/tarjeta13/imagen1.png" alt="La imagen proporciona tips para cortar telas con cuadros. Primero, se deben ubicar los patrones en el textil y alinear las lneas para que coincidan. Luego, se traza en los patrones las lneas correspondientes. Este proceso debe repetirse para cada una de las piezas de la prenda. Finalmente, una vez ubicados los patrones, se deben pintar y, con la tiza, agregar el margen de costura. Cada tip est ilustrado en un recuadro con diferentes colores de fondo: amarillo para los primeros y tercer tip, y morado para el segundo y cuarto tip. En la parte superior de la imagen se encuentra el ttulo Tips de corte para cuadros.")
          p.text-center #[b Ubicar los patrones en el textil y alinear para que coincidan las lneas. ]
      .col-sm-12.col-md-6.col-lg-3
        .tarjeta.color-primario.borde-redondo.p-4
