      .col-sm-12.col-md-6.col-lg-3
        .tarjeta.color-primario.borde-redondo.p-4
          img.p-4(src="@/assets/curso/temas/tema2/tarjeta13/imagen4.png" alt="La imagen proporciona tips para cortar telas con cuadros. Primero, se deben ubicar los patrones en el textil y alinear las lneas para que coincidan. Luego, se traza en los patrones las lneas correspondientes. Este proceso debe repetirse para cada una de las piezas de la prenda. Finalmente, una vez ubicados los patrones, se deben pintar y, con la tiza, agregar el margen de costura. Cada tip est ilustrado en un recuadro con diferentes colores de fondo: amarillo para los primeros y tercer tip, y morado para el segundo y cuarto tip. En la parte superior de la imagen se encuentra el ttulo Tips de corte para cuadros.")
