      p En el desarrollo de los contenidos se han identificado trminos tcnicos de la industria, los cuales se irn apropiando con mayor rigor en la prctica; la inmersin en la industria y el mercado permitir afianzar cada vez ms los conocimientos tericos; sin embargo, con el fin de sintetizar de una manera ms grfica se har la contextualizacin aplicada en diferentes tipos de materiales con una serie de moldes a escala que permitirn poner en prctica los conceptos explicados.
    
