        h2 2.7  Terminologa industrial de trazo y corte
    
    .row.justify-content-center.align-items-center.mb-5(data-aos="zoom-in-left")
      .col-sm-12.col-md-7.col-lg-7
        h3 #[b Tiqueteado ]
        p Consiste en rotular cada una de las piezas cortadas, identificando el lote, nombre de la pieza, talla y un consecutivo para contribuir en el orden del sistema operacional.
        .cajon.color-primario 
          p.p-5 #[b  Se usa tiqueteadora con su papel adhesivo; es importante definir la ubicacin en la pieza, se trabaja por el revs de la tela.  ]
      .col-7.col-md-3.col-lg-3.p-3
        img(src="@/assets/curso/temas/tema2/imagen55.svg")
                    
    .row.mb-4.mt-4(data-aos="zoom-in-left")
      h3.mb-4 Operaciones auxiliares de corte
      p Interviene personal auxiliar para apoyar el proceso. 

    AcordionA(tipo="b" clase-tarjeta="tarjeta tarjeta--purple")(data-aos="zoom-in-left")
      .row.borde-redondo-tmorada(titulo="Fusionado")
        .col-md-12.mb-4.mb-md-0
          p.p-5 Una vez se corta la tela y la entretela, para entretelar se utiliza una mquina fusionadora, que a travs de presin y calor adhiere la entretela a la tela. Es importante el control de temperatura segn el textil, ya que puede generar encogimiento.
      .row.borde-redondo-tmorada(titulo="Paqueteo")
        .col-md-12.mb-4.mb-md-0
          p.p-5 Consiste en separar por bloques las piezas cortadas, paqueteando la cantidad definida de todas las piezas que conforman la prenda, amarrando y conformando paquetes de 10 a 20 unidades; para as facilitar el control de produccin y de calidad. Una vez se paquetee es importante el almacenamiento, utilizando la tcnica de Kanban para entregarlas a ensamble.
      .row.borde-redondo-tmorada(titulo="Tecnologa en corte")
        .col-md-12.mb-4.mb-md-0
          p.p-5 La tecnologa en el rea de corte inicia con el trazo asistido por computador a travs de programas especializados, los cuales ahorran tiempo y espacio. Para el transporte de telas y paquetes cortados se usan bandas o carros, que son utilizados tambin para entregar los paquetes al rea de ensamble.
      .row.borde-redondo-tmorada(titulo="<i>Software</i> de corte")
